.movie-card {
  margin: 20px;
  /* width: 200px;
  height: 400px; */
}

div.poster-placeholder {
  width: 200px;
  height: 325px;
}

div.poster-container {
  /* animation: in 0.3s; */
  transition: 0.3s;
}

div.poster-container:hover {
  transform: scale(1.75, 1.75);
  position: absolute;
  padding: 10px;
  /* animation: out 0.3s; */
  transition: 0.3s;
}

img.movie-poster {
  width: 200px;
  height: 325px;
  object-fit: cover;
}

div.poster-container:hover img.movie-poster {
  object-fit: contain;
  filter: drop-shadow(10px 10px 15px black);
}

div.poster-overlay {
  display: block;
  position: absolute;
  width: 200px;
  height: 325px;
  background: rgba(0, 0, 0, 0);
  transition: 1s;
}

div.poster-overlay > div.progress-bar {
  background: grey;
  position: absolute;
  bottom: 0;
  width: 0;
  height: 7px;
  transition: 1s;
}

div.poster-overlay:hover {
  display: block;
  position: absolute;
  width: 200px;
  height: 325px;
  background: rgba(0, 0, 0, 0.6);
  transition: 2s linear;
}

div.poster-overlay:hover > div.progress-bar {
  background: grey;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 7px;
  transition: 2s linear;
}