div.movie-details {
    background: black;
    width: 100%;
    height: 50vh;
}

img.movie-details-background {
    position: absolute;
    width: calc(100% - 100px);
    height: 50vh;
    left: 50px;
    object-fit: cover;
    opacity: 0.2;
}

img.movie-details-poster {
    height: 50vh;
    position: relative;
    float: left;
    padding: 10px;
}

